import React, { Component } from "react";
import FlexWrapper from "./FlexWrapper";
import {
  Badge,
  Box,
  Button,
  Heading,
  Text,
  theme,
} from "@mechatronick/datum-design-system";
import styled, { css } from "styled-components";
import ButtonWithGatsbyLink from "./ButtonWithGatsbyLink";
import helperLib from "../helperLib";
import { FlexWrapperBorderLG } from "./FlexWrapperBorder";
import Fade from "react-reveal/Fade";

const SelectBadge = styled(Button)`
  ${props =>
    props.selected
      ? css`
          border: 4px solid ${theme.colors["darkPrimary"]};
          background-color: ${theme.colors["darkPrimary"]};
          color: ${theme.colors["trueWhite"]};
          box-shadow: ${theme.boxShadows[1]};
          &:hover {
            box-shadow: ${theme.boxShadows[2]};
          }
        `
      : css`
          border: 4px solid ${theme.colors["lightPrimary"]};
          color: ${theme.colors["darkPrimary"]};
          &:hover {
            background-color: ${theme.colors["lightPrimary"]};
            color: ${theme.colors["trueWhite"]};
            box-shadow: ${theme.boxShadows[2]};
          }
        `};
`;

SelectBadge.defaultProps = {
  selected: false,
  px: [2, 2, 3],
  py: [2, 2, 2],
  m: [1, 2, 3],
  f: [1, 2, 3],
};

const StyledTag = styled(Badge)`
  text-transform: none;
  margin: ${theme.space[1] / 2}px;
`;

class SelectOneInGroup extends Component {
  constructor(props) {
    super(props);
    let initialSelect = helperLib.generateBoolArray(props.data.length, false);
    initialSelect[0] = true;
    this.state = {
      select: initialSelect,
      index: 0,
      length: props.data.length,
    };
    this.handleChangeOption = this.handleChangeOption.bind(this);
  }

  componentDidMount() {}

  handleChangeOption(event) {
    const { id } = event.target;
    let newSelect = helperLib.generateBoolArray(this.state.length, false);
    newSelect[parseInt(id)] = true;
    this.setState({
      select: newSelect,
      index: parseInt(id),
    });
  }

  render() {
    const { select } = this.state;
    const { data } = this.props;
    return (
      <>
        <FlexWrapper wrap={"wrap"}>
          {data.map((eachCategory, index) => {
            return (
              <SelectBadge
                key={index}
                id={index}
                selected={select[index]}
                onClick={this.handleChangeOption}
              >
                {eachCategory.name}
              </SelectBadge>
            );
          })}
        </FlexWrapper>
        <Fade spy={select} exit={false}>
          <FlexWrapperBorderLG
            style={{
              textAlign: "center",
              alignItems: "center",
            }}
            wrap={"wrap"}
          >
            <Box width={[0, 1 / 3, 0]} />
            <Box width={[0, 1 / 3, 1 / 4]} p={[0, 2, 3]}>
              <img
                title={data[this.state.index].name}
                alt={data[this.state.index].name}
                width={"100%"}
                height={"auto"}
                src={data[this.state.index].svg}
              />
            </Box>
            <Box width={[0, 1 / 3, 0]} />
            <Box width={[1, 1, 3 / 4]} p={[2, 2, 3]}>
              <Heading.h3 bold>{data[this.state.index].heading}</Heading.h3>
              <Text.p>{data[this.state.index].description}</Text.p>
              <div>
                {data[this.state.index].tags.map(eachTag => {
                  return (
                    <StyledTag
                      px={1}
                      py={1}
                      fontSizes={1}
                      key={eachTag}
                      bg={"slate"}
                    >
                      {eachTag}
                    </StyledTag>
                  );
                })}
              </div>
              <ButtonWithGatsbyLink to={data[this.state.index].to}>
                {data[this.state.index].buttonText}
              </ButtonWithGatsbyLink>
            </Box>
          </FlexWrapperBorderLG>
        </Fade>
      </>
    );
  }
}

export default SelectOneInGroup;
