const helperLib = {
    oldValidEmail: function (email) {
        // eslint-disable-next-line
        const re = /^(([^<>()[\]\\.,;:\s@\"]+(\.[^<>()[\]\\.,;:\s@\"]+)*)|(\".+\"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;
        if (email !== undefined) {
            return re.test(email);
        }
        return false;
    },
    validEmail: function (emailAddress) {
        const sQtext = "[^\\x0d\\x22\\x5c\\x80-\\xff]";
        const sDtext = "[^\\x0d\\x5b-\\x5d\\x80-\\xff]";
        const sAtom = "[^\\x00-\\x20\\x22\\x28\\x29\\x2c\\x2e\\x3a-\\x3c\\x3e\\x40\\x5b-\\x5d\\x7f-\\xff]+";
        const sQuotedPair = "\\x5c[\\x00-\\x7f]";
        const sDomainLiteral = "\\x5b(" + sDtext + "|" + sQuotedPair + ")*\\x5d";
        const sQuotedString = "\\x22(" + sQtext + "|" + sQuotedPair + ")*\\x22";
        const sDomain_ref = sAtom;
        const sSubDomain = "(" + sDomain_ref + "|" + sDomainLiteral + ")";
        const sWord = "(" + sAtom + "|" + sQuotedString + ")";
        const sDomain = sSubDomain + "(\\x2e" + sSubDomain + ")*";
        const sLocalPart = sWord + "(\\x2e" + sWord + ")*";
        const sAddrSpec = sLocalPart + "\\x40" + sDomain; // complete RFC822 email address spec
        const sValidEmail = "^" + sAddrSpec + "$"; // as whole string

        const reValidEmail = new RegExp(sValidEmail);

        return reValidEmail.test(emailAddress);
    },
    atLeastTwoCharactersAndOnlyCharacter: function (text) {
        const re = /([A-Za-z])/g;
        let enoughLength = false;
        if (text !== undefined) {
            enoughLength = text.length >= 2;
            return (re.test(text) && enoughLength);
        }
        return false;
    },
    onlyValidNZPhoneNumber: function (number) {
        const re = /^[+]?[(]?[0-9]{3}[)]?[-\s.]?[0-9]{3}[-\s.]?[0-9]{3,6}$/im;
        let pureNumberString = number.replace(/\D/g, "");
        let numberOfDigits = pureNumberString.toString().length;
        return (re.test(number) || (numberOfDigits >= 9 && numberOfDigits <= 12));
    },
    onlyValidNumberInRange: function (number, min = 0, max = 5) {
        return (number >= min && number <= max);
    },
    textOnly: function (input) {
        const re = /(^[a-zA-Z ]+(\s*[a-zA-Z ]+)*$)|(^$)/; //allow for space & emtpy
        return re.test(input);
    },
    textAndNumberSlashOnlyForAddress: function (input) {
        const re = /(^[a-zA-Z0-9/, ]+(\s*[a-zA-Z0-9/, ]+)*$)|(^$)/; //allow for space & emtpy
        return re.test(input);
    },
    textAndNumberWithSpace: function (input) {
        const re = /(^[a-zA-Z0-9 ]+(\s*[a-zA-Z0-9 ]+)*$)|(^$)/; //allow for space & emtpy
        return re.test(input);
    },
    integerNumberOnly: function (input) {
        const re = /(^[0-9]+(\s*[0-9]+)*$)|(^$)/; //allow for space & emtpy
        return re.test(input);
    }
    ,
    numberWithSignAndFloatOnly: function (input) {
        const re = /(^-?[\d+-]+\.?[\d+-]*$)|(^$)/; // no space
        return re.test(input);
    },
    numberOnly: function (input) {
        const re = /^-?\d*?\d*$/; // allow for empty string
        return re.test(input);
    },
    currencyOnly: function (input) {
        return true;
    },
    numberWithSignAndFloatAndSpaceOnly: function (input) {
        const re = /(^-?[\d +-]+\.?[\d +-]*$)|(^$)/; // allow space
        return re.test(input);
    },
    characterFitForEmailOnly: function (input) {
        const re = /(^-?[A-Za-z\d@._]+\.?[A-Za-z\d@._]*$)|(^$)/; //do not allow for space
        return re.test(input);
    },
    minNum1digit: 0
    ,
    minNum3digits: 100
    ,
    maxNum2digits: 10
    ,
    maxNum24yo: 24
    ,
    maxNum9digits: 99999999
    ,
    formatRawStringIntoCurrencyString: function (input, min = 0, max = 99999999) {

        let minNum = min;

        let maxNum = max;

        const minString = minNum.toString();
        // const maxString = maxNum.toString();
        let step1 = input.toString().replace(/[^0-9.-]+/g, "");
        // console.log(step1);
        if (step1 === "") {
            step1 = minString;
        }
        let step2 = parseInt(step1);
        if (step2 > maxNum) {
            step2 = maxNum;
        } else if (step1 < minNum) {
            step2 = minNum;
        }
        // console.log(step2);
        return step2.toString().replace(/\B(?=(\d{3})+(?!\d))/g, ",");
    },
    lookupKeyByValue: function (object, value) {
        return Object.keys(object).find(key => object[key] === value);
    },
    checkIfNotNullUndefinedEmpty: function (value) {
        return (value !== null && value !== "" && value !== [] && value !== {} && value !== undefined);
    },
    checkIfNullUndefinedEmpty: function (value) {
        return (value === null || value === "" || value.length === 0 || value === {} || value === undefined);
    },
    checkArrayNotUndefinedOrEmpty: function (array) {
        return array !== undefined && array.length !== 0;
    }
    ,
    searchObject: function (arr, s) {
        let matches = [], i, key;

        for (i = arr.length; i--;)
            for (key in arr[i])
                if (arr[i].hasOwnProperty(key) && arr[i][key].indexOf(s) > -1)
                    matches.push(arr[i]);  // <-- This can be changed to anything

        return matches;
    },
    removeFromArray: function (arr, what) {
        let found = arr.indexOf(what);

        while (found !== -1) {
            arr.splice(found, 1);
            found = arr.indexOf(what);
        }
    },
    formatMonths: function (month, min = "0", max = "60") {
        const numMonth = parseInt(month);
        switch (numMonth) {
            case 0:
            case 1:
            case 2:
            case 3:
            case 4:
            case 5:
            case 6:
            case 7:
            case 8:
            case 9:
            case 10:
            case 11:
                return numMonth + " months";
            case parseInt(max):
                return "+ " + Math.floor(numMonth * 2 / 12) / 2 + " years";
            default:
                return Math.floor(numMonth * 2 / 12) / 2 + " years";
        }
    },
    removeCommaFromString: function (string) {
        return string.replace(/,/g, "");
    },
    arrayUnique: function (array) {
        let a = array.concat();
        for (let i = 0; i < a.length; ++i) {
            for (let j = i + 1; j < a.length; ++j) {
                if (a[i] === a[j])
                    a.splice(j--, 1);
            }
        }

        return a;
    },
    getKeysOfObjectThatContainSubString: function (object = {}, substring = "a string") {
        let output = [];
        for (let key in object) {
            if (object.hasOwnProperty(key)) {
                if (key.indexOf(substring) !== -1) {
                    output.push(key);
                }
            }
        }
        return output;
    },
    getKeysOfObjectThatContainSubStringExclude: function (object = {}, substring = "a string", exclude = "not this one") {
        let output = [];
        for (let key in object) {
            if (object.hasOwnProperty(key)) {
                if (key.indexOf(substring) !== -1 && key.indexOf(exclude) === -1) {
                    output.push(key);
                }
            }
        }
        return output;
    },
    getValueOfObjectThatContainSubString: function (object = {}, substring = "a string") {
        let output = [];
        for (let key in object) {
            if (object.hasOwnProperty(key)) {
                if (key.indexOf(substring) !== -1) {
                    output.push(object[key]);
                }
            }
        }
        return output;
    },
    getNumberOutOfString: function (input) {
        return parseInt(input.replace(/\D/g, ""));
    },
    filterNumberOutOfString: function (input) {
        return input.replace(/[0-9]/g, "");
    },
    capitalFirstLetter: function (input) {
        if (input) {
            return input.charAt(0).toUpperCase() + input.slice(1).toLowerCase();
        }
        return input;
    },
    checkIfObjectContainKey: function (object, input) {
        const keysArray = Object.keys(object);
        return keysArray.indexOf(input) !== -1;
    },
    checkIfObjectContainValue: function (object, input) {
        return Object.values(object).indexOf(input) > -1;
    },
    hasNumber: function (myString) {
        return /\d/.test(myString);
    },
    generateBoolArray: function (num = 0, value = false) {
        let a = [];
        for (let i = 0; i < num; i++) {
            a.push(value);
        }
        return a;
    }
};

export default helperLib;